<template>
  <div style="width: 25px; height: 25px; justify-content: center; padding: 0; margin-right: 10px;">
    <span v-if="!exec" class="white_hover"
          style="font-size:25px; align-items: start"
          v-on:click="this.click()">&#8634;</span>
    <div v-else class="spinner-border grey_letter reload" role="status"></div>
  </div>

</template>

<script>
export default {
  name: "ReloadButton2",
  props: ['func'],
  methods: {
    async click(){
      this.exec = true;
      await this.func();
      this.exec = false;
    }
  },
  data(){
    return{
      exec: false
    }
  }
}
</script>

<style>
.reload{
  justify-self: end;
  width: 25px;
  height: 25px;
  margin-top: 7.5px;
  margin-bottom: 5px;
  margin-right: 10px;
  font-size: 12.5px;
}
</style>