<template>
  <div class="nav-item">
    <button v-on:click="this.on_click(index)" class="btn">
      <h5 class="purple_letter" v-if="this.active === index">{{text}}</h5>
      <h5 class="white_hover" v-else>{{text}}</h5>
    </button>
    <div v-if="this.active === index" class="hr_nav purple_back"></div>
  </div>
</template>

<script>
export default {
  name: "NavTitle",
  props: ['text', 'index', 'active', 'on_click'],
}
</script>

<style scoped>
.btn{
  background: transparent;
  border-color: transparent;
}
.hr_nav{
  width: 100px;
  height: 2px;
  position: absolute;
  margin-top: 15px;
}
</style>