<template>
  <button v-if="!exec&&color" type="button" class="btn white_hover" :class="color" v-on:click="this.click()"
          :disabled="(isRunning & mutable) || disabled">
    {{ text }}
    <p v-if="isRunning & mutable">Not available while running!</p>
    <slot v-else-if="disabled"/>
  </button>
  <button v-else-if="!exec" type="button" class="blue_button btn white_hover" v-on:click="this.click()"
          :disabled="(isRunning & mutable) || disabled">
    {{ text }}
    <p v-if="isRunning & mutable">Not available while running!</p>
    <slot v-else-if="disabled"/>
  </button>
  <div v-else class="spinner-border pale_letter" role="status"/>
</template>

<script>
export default {
  name: "BlueButton",
  props: ['onClick', 'text', 'mutable', 'isRunning', 'color', 'disabled'],
  methods: {
    async click(){
      this.exec = true;
      await this.onClick();
      this.exec = false;
    }
  },
  data(){
    return{
      exec: false
    }
  }
}
</script>

<style scoped>
.btn{
  width: 150px;
  margin-top: 0px;
  margin-bottom: 0px;
  justify-self: center;
  border-color: transparent;
}
.pale_letter{
  justify-self: center;
  width: 38px;
  height: 38px;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 37px;
  margin-left: 37px;
}
</style>