<template>
  <div v-if="state_fetch.state==='failed'&&this.state==='fetching'"
       class="row btn_row">
    <PurpleButton text="Back" :on-click="wE(this.backToNone)" style="align-self: end"/>
  </div>
  <MessagePool v-if="state_fetch.state==='failed'&&this.state==='fetching'"
               title="Failed to fetch traits" :text="'Left: '+state_fetch.left+' Missed: '+state_fetch.missed" class="error_back"/>
  <div v-if="state_fetch.state==='failed'&&this.state==='fetching'"
       class="row btn_row" style="justify-content: end;">
    <PurpleButton text="Retry" :on-click="wE(this.fetchTraits)" style="align-self: end" :is-running="isRunning" :mutable="true"/>
  </div>
  <div v-if="state_fetch.state==='interrupted'&&this.state==='fetching'"
       class="row btn_row">
    <PurpleButton text="Back" :on-click="wE(this.backToNone)" style="align-self: end"/>
  </div>
  <MessagePool v-if="state_fetch.state==='interrupted'&&this.state==='fetching'"
               title="Paused Fetching" :text="'Left: '+state_fetch.left+' Missed: '+state_fetch.missed" class="blue_back"/>
  <div v-if="state_fetch.state==='interrupted'&&this.state==='fetching'"
       class="row btn_row" style="justify-content: end;">
    <PurpleButton text="Proceed" :on-click="wE(this.fetchTraits)" style="align-self: end" :is-running="isRunning" :mutable="true"/>
  </div>
  <MessagePool v-if="isRunning&&this.state==='fetching'" :is-spinning="true"
               :text="'Left: '+state_fetch.left" class="pool_back">
  </MessagePool>
  <div v-if="state_fetch.state==='interrupted'&&this.state==='fetching'"
       class="row btn_row">
    <PurpleButton text="Back" :on-click="wE(this.backToNone)" style="align-self: end"/>
  </div>
  <div v-if="this.state==='none'" class="container1 container rounded-4 pool_back">
    <div class="row"><h5 class="purple_letter">Reward Tokens</h5></div>
    <div class="row" style="margin-top: 0; padding-top: 0">
      <DisplayTrait name="Reward Tokens" style="color: transparent"/>
      <div v-for="index in this.config.tokens.length" :key="index">
        <DisplayToken :value="config.tokens[index-1]" :is-short="true"/>
      </div>
    </div>
    <div class="hr grey_back" style="margin-top: 0"/>
    <div class="row" style="margin-top: 0; padding-top: 0">
      <DisplayTrait name="Base Values"/>
      <div v-for="index in this.config.tokens.length" :key="index">
        <input class="input white_letter"
               :value="this.base_values[index-1]"
               @input="event => this.base_values[index-1] = event.target.value"
               placeholder="0.0" aria-label="reward" aria-describedby="basic-addon3">
      </div>
    </div>
  </div>
  <div class="row btn_row" v-if="this.state==='none'">
    <PurpleButton  text="Configure" :mutable="true" :on-click="configure"
                  :is-running="isRunning||(this.state==='fetching'&&this.state_fetch!=='completed')"/>
    <div style="width: 100px"/>
    <PurpleButton text="Fetch Traits" :on-click="wE(this.fetchTraits)" :is-running="isRunning" :mutable="true"/>
  </div>
</template>

<script>
import PurpleButton from "@/elements/PurpleButton";
import MessagePool from "@/elements/MessagePool";
import Config from "../../data/config.json";
import DisplayToken from "@/elements/DisplayToken";
import DisplayTrait from "@/elements/DisplayTrait";
import {parseFloat} from "@/methods";
export default {
  name: "RewardsNone",
  components: {
    DisplayTrait,
    DisplayToken,
    MessagePool,
    PurpleButton
  },
  props: ['show', 'isRunning', 'state', 'wE', 'post', 'postPageState', 'state_check', 'state_fetch'],
  methods:{
    async fetchTraits(){
      if(await this.post({}, "/fetch_traits")){
        await this.postPageState("fetching", "rewards")
      }
    },
    async configure(){
      let a = [];
      for(let i = 0; i < this.config.tokens.length; i++){
        a.push(parseFloat(this.base_values[i], this.config.decimals[i]));
      }
      const values = {base_value: a, used_traits: {}};
      if(await this.post(values, "/set_base")){
        await this.postPageState("scripted", "rewards");
      }
    },
    async backToNone(){
      await this.postPageState("none", "rewards");
    },
  },
  watch: {
    isRunning: {
      deep: true,
      async handler(newVal, oldVal) {
        console.log(this.state, this.state_fetch, newVal, oldVal)
        if (!newVal && oldVal) {
          console.log("ended");
          if(this.state==='fetching'&&this.state_fetch.state==='completed'){
            await this.postPageState("configure", "rewards");
            this.show(true, "Successfully fetched traits");
          }
          if(this.state==='checking'&&this.state_check.state==='completed'){
            await this.postPageState("configure", "rewards");
            this.show(true, "Successfully checked hashes");
          }
        }
      }
    }
  },
  data(){
    return{
      config: Config,
      base_values: Array(5).fill("0"),
    }
  }
}
</script>

<style scoped>
.input{
  background: transparent;
  width: 125px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-left-color: transparent;
  border-bottom: transparent;
  text-align: right;
}
.input::placeholder {
  text-align: right;
}
</style>