<template>
  <h6 style="width: 200px; padding-top: 5px;" class="blue_letter">{{slice(name)}}</h6>
</template>

<script>
export default {
  name: "DisplayTrait",
  props: ['name'],
  methods:{
    slice(text){
      if(text.length < 22) return text;
      return text.slice(0, 18) + "...";
    }
  }
}
</script>
