<template>
  <div class="row" style="margin-right: 0px;">
    <DisplayToken :value="symbol" :is-short="true" style="padding-top: 0; padding-bottom:0; text-align: start" :no-redirect="true"/>
    <a v-bind:href="prefix+value+postfix" class="btn white_hover" style="padding: 0; text-decoration: none; border: none;">
      {{name}}
    </a>
  </div>
  <div v-if="underline" class="hr grey_back"></div>
</template>

<script>
import DisplayToken from "@/elements/DisplayToken";
import {displayName} from "@/methods";
import env from "../../env";
export default {
  name: "DisplayName",
  props: ['symbol', 'value', 'underline'],
  components: {
    DisplayToken
  },
  methods:{
    async refreshName(){
      this.name = await displayName(this.value);
    }
  },
  async mounted() {
    this.refreshName()
  },
  data(){
    return{
      name: "_",
      prefix: env.explorerAddressPrefix,
      postfix: env.explorerAddressPostfix
    }
  }
}
</script> bn bn

<style scoped>
.text{
  padding: 0px 10px 0px 10px;
}
</style>