<template>
  <div v-if="state==='none'||(index>active)" class="row rounded-2 line script-none">
    {{name}}
  </div>
  <div v-else-if="index<active||state==='completed'" class="row rounded-2 line script-completed">
    {{name}}
  </div>
  <div v-else-if="index===active&state==='running'" class="row rounded-2 line script-exec">
    {{name}}
  </div>
  <div v-else-if="index===active&state==='interrupted'" class="row rounded-2 line script-interrupted">
    {{name}}
  </div>
  <div v-else-if="index===active&state==='failed'" class="row rounded-2 line script-failed">
    {{name}}
  </div>
</template>

<script>
export default {
  name: "ScriptLine",
  props: ['name', 'index', 'active', 'state']
}
</script>

<style scoped>
.line{
  margin: 5px 10px 5px 10px;
  padding: 7.5px 15px 7.5px 15px;
}
</style>