<template>
  <div class="row">
    <div class="white_letter text">{{title}}</div>
    <div class="in_row">
      <input class="input rounded-1 log_back blue_letter" style="margin-left: auto"
             :value="this.d"
             @input="event => this.d = event.target.value"
             placeholder="day" aria-label="count" aria-describedby="basic-addon3">
      <input class="input rounded-1 log_back blue_letter"
             :value="this.h"
             @input="event => this.h = event.target.value"
             placeholder="hour" aria-label="count" aria-describedby="basic-addon3">
      <input class="input rounded-1 log_back blue_letter"
             :value="this.m"
             @input="event => this.m = event.target.value"
             placeholder="min" aria-label="count" aria-describedby="basic-addon3">
      <input class="input rounded-1 log_back blue_letter"
             :value="this.s"
             @input="event => this.s = event.target.value"
             placeholder="sec" aria-label="count" aria-describedby="basic-addon3">
    </div>
  </div>
  <div v-if="underline" class="hr grey_back"></div>
</template>

<script>
import {parseTime} from "@/methods";
import {useLocalStorage} from "@vueuse/core/index";
export default {
  name: "TimeInput",
  props: ['title', 'underline', 'wE'],
  data(){
    return{
      d: useLocalStorage('inputDays', ""),
      h: useLocalStorage('inputHours', ""),
      m: useLocalStorage('inputMonth', ""),
      s: useLocalStorage('inputSeconds', "")
    }
  },
  methods:{
    async gatherDate(){
      return await parseTime(this.d, this.h, this.m, this.s);
    },
    flush(){
      this.d = "";
      this.h = "";
      this.m = "";
      this.s = "";
    }
  }
}
</script>

<style scoped>
.text{
  padding: 0px 10px 0px 10px;
}
.in_row{
  grid-auto-flow: column;
  justify-content: space-between;
}
.input{
  margin-left: 10px;
  margin-right: 0px;
  width: 50px;
  padding: 0px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-left-color: transparent;
  border-bottom: transparent;
  text-align: right;
}
.input::placeholder {
  text-align: right;
}
</style>