<template>
  <div class="blackout">
    <div class="container3 container rounded-4 pool_back">
      <div class="row" style="margin-top: 5px">
        <select class="form-select selector white_hover" aria-label="add reward token"
                @input="event => {this.activeToken = event.target.value; this.updateCommission(this.activeDiscountIndex)}">
          <option hidden>Token</option>
          <option v-for="index in this.tokens.length" :key="index" :value="this.config.tokens[index-1]">{{this.tokens[index-1]}}</option>
        </select>
      </div>
      <div class="row">
        <input class="input blue_letter"
               :value="this.amount"
               @input="event => this.amount = event.target.value"
               placeholder="amount" aria-label="amount" aria-describedby="basic-addon3">
      </div>
      <div v-if="activeDiscountIndex>=0&&this.activeToken" class="row purple_letter" style="margin-left: 20px; align-content: start">
        Commission: {{this.commission}}
      </div>
      <div v-else-if="this.activeToken" class="row blue_letter" style="margin-left: 20px; align-content: start">
        Commission: {{this.commission}}
      </div>
      <div class="hr grey_back" style="margin-left: 20px; margin-right: 20px;"/>
      <div class="row" v-for="index in this.discounts.length" :key="index" style="justify-self: center">
        <button v-if="this.discounts[index-1].ticket" type="button" class="btn pool_back" v-on:click="this.updateCommission(index-1)"
                :disabled="activeDiscountIndex===(index-1)" style="width: 400px;" :class="discounts[index-1].used? 'discount_used': 'discount'">
          <DisplayName :symbol="discounts[index-1].collection" :value="discounts[index-1].mint"/>
          <div v-if="activeDiscountIndex===(index-1)" class="hr purple_back" style="margin-left: 20px; margin-right: 20px;"/>
          <div v-else class="hr" style="margin-left: 20px; margin-right: 20px;" :class="discounts[index-1].used? 'success_back': 'grey_back'"/>
          <DisplayRow symbol="Commission" :value="this.calcCommission(config, activeToken, discounts[index-1])"/>
        </button>
        <button v-else class="btn pool_back discount_busy" style="width: 400px;" :disabled="true">
          <DisplayName :symbol="discounts[index-1].collection" :value="discounts[index-1].mint"/>
          <div class="hr error_back" style="margin-left: 20px; margin-right: 20px;"/>
          <DisplayRow symbol="Commission" :value="this.calcCommission(config, activeToken, discounts[index-1])"/>
        </button>
      </div>
      <div v-if="!isReady" class="spinner-border pale_letter" role="status"/>
      <div class="row btn_row" style="margin-top: 20px; margin-bottom: 10px">
        <PurpleButton text="Fund" :on-click="wE(fund)" color="success_button" :disabled="this.amount===''||!this.activeToken"/>
        <PurpleButton text="Cancel" :on-click="wE(back)" class="error_button"/>
      </div>
    </div>
  </div>
</template>

<script>
import Config from "../../data/config.json";
import {displayPubkey, parseRoyalty, searchForDiscount, calcCommission, addBalanceTx, parseFloat} from "@/methods"
import DisplayRow from "@/elements/DisplayRow";
import PurpleButton from "@/elements/BlueButton";
import DisplayName from "@/elements/DisplayName";
export default {
  name: "AddBalanceAlert",
  props: ['wE', 'signTx', 'log', 'show', 'open'],
  components: {DisplayName, PurpleButton, DisplayRow},
  async mounted(){
    for(let i = 0; i < this.config.tokens.length; i++){
      this.tokens.push(await displayPubkey(this.config.tokens[i], true, true))
    }
    this.royalty = await parseRoyalty(this.config);
    this.discounts = await searchForDiscount(this.config);
    this.isReady = true;
    this.updateCommission(-1);
  },
  methods:{
    async fund(){
      const index = this.config.tokens.indexOf(this.activeToken);
      const amount = parseFloat(this.amount, this.config.decimals[index]);
      const tx = await addBalanceTx(amount, this.config, this.activeToken, this.royalty, this.activeDiscountIndex, this.discounts);
      if(await this.signTx(tx, [], this.log)){
        this.back();
      }
    },
    back(){
      this.open(true);
    },
    updateCommission(discountIndex){
      if(discountIndex>=0){
        this.activeDiscountIndex=discountIndex;
        this.commission = calcCommission(this.config, this.activeToken, this.discounts[discountIndex]);
      }
      else{
        this.commission = calcCommission(this.config, this.activeToken, this.royalty);
      }
    },
  },
  data(){
    return{
      config: Config,
      amount: "",
      tokens: [],
      activeToken: undefined,
      activeDiscountIndex: -1,
      royalty: undefined,
      discounts: [],
      calcCommission: calcCommission,
      commission: 0,
      isReady: false
    }
  }
}
</script>

<style scoped>
.blackout {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0; left: 0;
  background: rgba(0,0,0,.5);
  z-index: 5;
  display: none;
}
.container3{
  max-width: 500px;
  width: 50%;
  min-height: 0px;
  display: inline-grid;
  justify-self: center;
  padding-bottom: 20px;
  padding-top: 20px;
  justify-content: center;
  position: fixed;
  margin-top: 10%;
}
.input{
  background: transparent;
  margin-right: 20px;
  margin-left: 20px;
  width: 400px;
  padding: 5px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-left-color: transparent;
  border-bottom: transparent;
  text-align: right;
  outline: 1px solid #525252;
  border-radius: 5px;
}
.input::placeholder {
  text-align: right;
}
.selector{
  width: 400px;
  margin-right: 0px;
  margin-left: 20px;
  border-color: transparent;
  background-color: transparent;
  text-align: center;
  outline: 2px solid #d0d0d0;
  border-radius: 5px;
}
.form-select option{
  border: none;
}
.pale_letter{
  justify-self: center;
  width: 38px;
  height: 38px;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 37px;
  margin-left: 37px;
}
</style>