<template>
  <a v-if="this.isShort&&!noRedirect" v-bind:href="prefix+value+postfix" class="btn blue_hover"
     style="background-color: transparent; border-color: transparent; width: 125px; text-align: end; padding-right: 0px;">
    {{name}}
  </a>
  <a v-else-if="!noRedirect" v-bind:href="prefix+value+postfix" class="btn blue_hover" style="background-color: transparent; border-color: transparent;">
    {{name}}
  </a>
  <div v-else-if="this.isShort" class="btn blue_hover"
     style="background-color: transparent; border-color: transparent; width: 125px; text-align: end; padding-right: 0px;">
    {{name}}
  </div>
  <div v-else class="btn blue_hover" style="background-color: transparent; border-color: transparent;">
    {{name}}
  </div>
</template>

<script>
import {displayPubkey} from "@/methods";
import env from "../../env";
export default {
  name: "DisplayToken",
  props: ["value", 'isShort', 'noRedirect'],
  methods: {
    async display(value){
      this.name = await displayPubkey(value, this.isShort, true);
    }
  },
  mounted() {
    this.display(this.value);
  },
  data(){
    return{
      name: "_",
      prefix: env.explorerAddressPrefix,
      postfix: env.explorerAddressPostfix
    }
  }
}
</script>
