<template>
  <div class="row btn_row">
    <PurpleButton v-if="this.state_evm.state==='none'&&(this.plot? this.plot.length>0 : false)" text="Run" :on-click="run" :mutable="true" :is-running="isRunning"
                  :disabled="instance? this.instance.state===1: true" style="justify-self: start"><p>Pause Instance first!</p></PurpleButton>
    <PurpleButton v-if="this.state_evm.state==='interrupted'||this.state_evm.state==='failed'" text="Proceed" :on-click="proceed" :mutable="true" :is-running="isRunning"
                  :disabled="instance? this.instance.state===1: true" style="justify-self: start"><p>Pause Instance first!</p></PurpleButton>
    <BlueButton v-if="this.state_evm.state==='running'" text="Pause" :on-click="pause" :mutable="false" :is-running="false" style="justify-self: end"/>
  </div>
  <MessagePool v-if="this.state_evm.state==='running'" class="pool_back" :text="'Line: '+this.state_evm.row.toString()" :is-spinning="true"/>
  <MessagePool v-if="this.state_evm.state==='interrupted'" class="pool_back" :title="'Paused on line: '+this.state_evm.row.toString()" text=""/>
  <MessagePool v-if="this.state_evm.state==='completed'" class="success_back" title="Successfully run every script" text=""/>
  <MessagePool v-if="this.state_evm.state==='failed'" class="error_back" :title="'Failed on line: '+this.state_evm.row.toString()" text=""/>
  <MessagePool v-if="this.state_evm.state!=='completed'&&(this.plot? this.plot.length===0 : true)" class="pool_back" text="No scripts to run"/>
  <div class="outside_container container rounded-4 pool_back" v-if="this.plot.length > 0">
    <div class="inside_container container scroll">
      <div v-for="index in this.plot.length" :key="index">
        <ScriptLine :name="this.plot[index-1]" :index="index-1" :active="this.state_evm.left" :state="this.state_evm.state"/>
      </div>
    </div>
  </div>
</template>

<script>
import ScriptLine from "@/elements/ScriptLine";
import MessagePool from "@/elements/MessagePool";
import PurpleButton from "@/elements/PurpleButton";
import BlueButton from "@/elements/BlueButton";
export default {
  name: "EngineState",
  props: ['isRunning', 'state_evm', 'post', 'instance'],
  components: {
    ScriptLine,
    MessagePool,
    PurpleButton,
    BlueButton
  },
  methods: {
    async getPlot(){
      const answer = await this.post({}, "/get_scripts");
      if(answer){
        this.plot = answer.plot;
      }
    },
    async run(){
      console.log("run")
      await this.post({}, "/run_engine");
    },
    async pause(){
      await this.post({}, "/pause_engine");
    },
    async proceed(){
      await this.post({}, "/continue_engine");
    }
  },
  data(){
    return{
      plot: [],
    }
  },
  mounted() {
    this.getPlot();
  }
}
</script>

<style scoped>
.outside_container{
  max-width: 750px;
  width: 75%;
  min-height: 0;
  margin: 10px 0 10px 0;
  display: inline-grid;
  justify-self: center;
  padding: 20px 15px 20px 10px;
}
.inside_container{
  max-height: 60vh;
  display: inline-grid;
  justify-self: center;
  padding: 0;
  overflow-y: scroll;
}
.container::-webkit-scrollbar{
  position: fixed;
  margin-left: -20px;
}
</style>