<template>
  <NavBar :active="this.activePage" :on_click="this.barClicked"/>
  <LogFlow :log-buf="logBuf" :open-log="openLog" :is-log-opened="isLogOpened"/>
  <div id="mainPool" class="container container_main">
    <EngineState v-if="activePage===4" :post="postToServer" :is-running="this.isRunning" :state_evm="this.state_evm" :instance="instance"/>

    <InstanceNone v-else-if="activePage===1&&state_instance==='none'" :w-e="wE" :show="show"
                  :wallet="this.wallet?  this.wallet.publicKey.toBase58(): 'none'" :post="postToServer" :post-page-state="postPageState"/>

    <InstanceInit v-else-if="activePage===1&&state_instance==='init'" :w-e="wE" :is-running="isRunning" :show="show"
                   :wallet="this.wallet?  this.wallet.publicKey.toBase58(): 'none'" :post="postToServer" :post-page-state="postPageState"/>
    <InstanceConfigured  v-else-if="activePage===1&&state_instance==='configured'" :w-e="wE" :is-running="isRunning" :show="show" :post="postToServer"
                  :post-page-state="postPageState" :sign-tx="signTx" :log="log" :wallet="this.wallet?  this.wallet.publicKey.toBase58(): 'none'"/>

    <InstanceState v-else-if="activePage===1&&state_instance==='deployed'" :w-e="wE" :is-running="isRunning" :show="show"
                   :wallet="this.wallet?  this.wallet.publicKey.toBase58(): 'none'" :post="postToServer" :sign-tx="signTx" :log="log"
                    :instance="instance" :refresh-instance="refreshInstance"/>

    <LoaderState v-else-if="activePage===3" :w-e="wE" :is-running="isRunning" :post="postToServer" :sign-tx="signTx" :log="log"
                 :wallet="this.wallet?  this.wallet.publicKey.toBase58(): 'none'" :show="show" :instance="instance"/>

    <RewardsNone v-else-if="activePage===2&&(state_rewards==='none'||state_rewards==='fetching'||state_rewards==='checking')" :w-e="wE" :is-running="isRunning" :post="postToServer" :post-page-state="postPageState"
                 :state="state_rewards" :state_check="state_check" :state_fetch="state_fetch" :show="show"/>

    <RewardsConfigure v-else-if="activePage===2&&state_rewards==='configure'" :show="show"
                      :w-e="wE" :is-running="isRunning" :post="postToServer" :post-page-state="postPageState"/>

    <RewardsState v-else-if="activePage===2&&state_rewards==='scripted'" :show="show"
                  :w-e="wE" :is-running="isRunning" :post="postToServer" :post-page-state="postPageState" :is-verified="isVerified"/>
    <GreetingPage v-else/>
  </div>
</template>

<script>
import {useAnchorWallet} from 'solana-wallets-vue';
import {reactive} from "vue";
import env from '../env';
import { Provider} from '@project-serum/anchor';
import { computed } from 'vue';
import NavBar from "@/elements/NavBar";
import {useLocalStorage} from '@vueuse/core';
import LogFlow from "@/elements/LogFlow";
import EngineState from "@/pages/EngineState";
import InstanceInit from "@/pages/InstanceInit";
import InstanceState from "@/pages/InstanceState";
import InstanceConfigured from "@/pages/InstanceConfigured";
import GreetingPage from "@/pages/GreetingPage";
import Swal from "sweetalert2";
import LoaderState from "@/pages/LoaderState";
import RewardsNone from "@/pages/RewardsNone";
import InstanceNone from "@/pages/InstanceNone";
import RewardsConfigure from "@/pages/RewardsConfigure";
import RewardsState from "@/pages/RewardsState";
import Config from "../data/config.json";
import {parseInstance} from "@/methods";
import {PublicKey} from "@solana/web3.js";


export default {
  name: 'App',
  components: {
    RewardsState,
    RewardsConfigure,
    InstanceNone,
    RewardsNone,
    LoaderState,
    LogFlow,
    NavBar,
    EngineState,
    InstanceState,
    InstanceInit,
    InstanceConfigured ,
    GreetingPage
  },
  methods:{
    barClicked(name){
      this.activePage = name;
    },
    show(res, msg){
      if(res){
        Swal.fire({
          position: 'top',
          title: msg,
          showConfirmButton: false,
          timer: 1000,
          background: "#2ED69B",
          color: "#FFFDFD",
        });
        this.log(msg, 1);
      }
      else{
        Swal.fire({
          position: 'top',
          title: msg,
          showConfirmButton: false,
          timer: 1000,
          background: "#f18181",
          color: "#FFFDFD",
        });
        this.log(msg, 2);
      }
    },
    wE(func){
      return (async () => {
        try{
          return await func();
        }
        catch(e){
          this.show(false, e.toString());
          return undefined;
        }
      });
    },
    openLog(isOpen){
      this.isLogOpened = isOpen;
      if(isOpen){
        document.getElementById("mainPool").style.marginLeft = "30%";
        document.getElementById("mainPool").style.marginRight = "5%";
      }
      else{
        document.getElementById("mainPool").style.marginLeft = "15%";
        document.getElementById("mainPool").style.marginRight = "15%";
      }
    },
    async log(text, type){
      this.logBuf.push({text: text, type: type});
      this.postToServer({text: text, type: type}, "/log_back");
    },
    async postPageState(state, type){
      if((await this.postToServer({type: type, state: state}, "/set_page"))){
        switch(type){
          case "instance":
            this.state_instance = state;
            return;
          case "rewards":
            this.state_rewards = state;
            return;
        }
      }
    },
    async loadStates(){
      try {
        const answer = await this.postToServer({}, "/load_states");
        if (!answer) return;
        for (let type in answer.states) {
          switch (type) {
            case "EVM":
              this.state_evm = answer.states[type];
              break;
            case "Fetch":
              this.state_fetch = answer.states[type];
              break;
            case "Check":
              this.state_check = answer.states[type];
              break;
          }
        }
        this.state_instance = answer.pages.instance;
        this.state_rewards = answer.pages.rewards;
      }
      catch(e){
        console.log(e)
      }
    },
    updateStates(state){
      if(!state) return;
      const container = state[0];
      const base = state[1];
      this.isRunning = base.isRunning;
      this.isVerified = base.isVerified;
      switch (base.type){
        case "EVM":
          this.state_evm = container;
          return;
        case "Fetch":
          this.state_fetch = container;
          return;
        case "Check":
          this.state_check = container;
          return;
      }
    },
    async refreshState(){
      await this.postToServer({}, "/refresh_states");
    },
    async postToServer(body, api, suppressMSG){
      if(!suppressMSG){
        suppressMSG = false;
      }
      try {
        const requestOptions = {
          method: "POST",
          headers: {"Content-Type": "application/json"},
          body: JSON.stringify(body)
        };
        const response = await fetch(" http://localhost:" + env.PORT + "/api"+api, requestOptions);
        const answer =  await response.json();
        if(answer.msg && (!suppressMSG)){
          this.show(answer.res, answer.msg);
          if(answer.res){
            this.log(answer.msg, 1);
          }
          else{
            this.log(answer.msg, 2);
          }
        }
        this.updateStates(answer.state);
        if(answer.logBuf){
          this.logBuf = this.logBuf.concat(Array.from(answer.logBuf));
        }
        if(!answer.res) return undefined;
        return answer;
      }
      catch(e){
        this.show(false, "Server not responds");
        return undefined;
      }
    },
    async refreshInstance(){
      try{
        const instance = await parseInstance(new PublicKey(this.config.instance));
        if(instance){
          this.instance = instance;
        }
        if(this.instance.loader.toString() !== this.config.loader.toString()){
          this.config.loader = this.instance.loader;
          this.postToServer(this.config, "/set_config");
        }
        return this.instance;
      }
      catch (e){
        this.instance = undefined;
        return undefined;
      }
    }
  },
  async mounted() {
    await this.loadStates();
  },
  watch: {
    isRunning: {
      deep: true,
      handler(newVal, oldVal) {
        if (newVal && !oldVal) {
          //this.refreshState()
          this.interval = setInterval(this.refreshState, 1000);
        }
        if (!newVal && oldVal) {
          clearInterval(this.interval);
        }
      }
    }
  },
  data(){
    return{
      activePage: useLocalStorage('activePageIndex', 1),
      isLogOpened: false,
      isRunning: false,
      isVerified: false,
      state_instance: "none",
      logBuf: [],
      state_rewards: "none",
      config: Config,
      instance: undefined,
      state_evm: {
        script: "none",
        row: 0,
        left: 0,
        missed: 0,
        state: "none"
      },
      state_fetch: {
        script: "none",
        row: 0,
        left: 0,
        missed: 0,
        state: "none"
      },
      state_check: {
        script: "none",
        row: 0,
        left: 0,
        missed: 0,
        state: "none"
      }
    }
  },
  setup(){
    const preflightCommitment = 'processed'
    const commitment = 'confirmed'
    const wallet = reactive(useAnchorWallet());
    const provider = computed(() => new Provider(env.connection, wallet.value, { preflightCommitment, commitment }));
    const showError = (text) => {
      Swal.fire({
        position: 'top',
        imageHeight: 500,
        icon: 'error',
        title: text,
        showConfirmButton: false,
        timer: 1000,
        background: "#1E2424",
        color: "#FFFDFD",
        iconColor: "#f18181"
      });
    }
    const showSuccess = (text) => {
      Swal.fire({
        position: 'top',
        imageHeight: 500,
        icon: 'success',
        title: text,
        showConfirmButton: false,
        timer: 1000,
        background: "#1E2424",
        color: "#FFFDFD",
        iconColor: "#2ED69B"
      });
    }
    const signTx = async (tx, signers, log) => {
      try {
        const tXH = await provider.value.send(tx, signers, {preflightCommitment: 'processed', commitment: 'confirmed'});
        log(tXH, 0);
        showSuccess("Transaction succeed");
        return true;
      }
      catch(e){
        console.log(e);
        showError("Transaction failed");
        return false
      }
    }
    return{
      wallet,
      provider,
      showError,
      showSuccess,
      signTx
    }
  },
}
</script>

<style>
@import './colors.css';
@import './styles.css';
.container_main{
  display: grid;
  justify-self: center;
  transition: 0.5s;
}
</style>

