<template>
  <div class="row btn_row">
    <PurpleButton text="Back" :on-click="wE(this.back)" :mutable="true" :is-running="isRunning"/>
  </div>
  <div class="container1 container rounded-4 pool_back">
    <DisplayRow symbol="Owner" :value="config.owner" :underline="true"/>
    <DisplayRow symbol="Collection" :value="config.collection" :underline="true" :is-token="true"/>
    <DisplayRow symbol="Loader" :value="config.loader" :underline="true"/>
    <DisplayRow symbol="Specification" :value="displayInstanceSpec(this.config.spec)"/>
    <DisplayRow symbol="UnitTime" :value="displayTime(BigInteger(config.unit_time))" :underline="true"/>
    <div class="row">
      <h5 class="purple_letter">Reward Tokens</h5>
    </div>
    <div v-for="index in this.config.tokens.length" :key="index">
      <DisplayRow :value="this.config.tokens[index-1]" symbol="" :is-token="true"/>
    </div>
  </div>
  <PurpleButton text="Deploy" :on-click="wE(this.deploy)"/>
</template>

<script>
import PurpleButton from "@/elements/PurpleButton";
import DisplayRow from "@/elements/DisplayRow";
import Config from "../../data/config.json";
import {displayTime, displayInstanceSpec, deployInstanceTx} from "@/methods";
import BigInteger from "big-integer";
export default {
  name: "InstanceInit",
  props: ["wE", 'postPageState', 'post', 'isRunning', 'show', 'signTx', 'wallet', 'log'],
  components: {
    PurpleButton,
    DisplayRow
  },
  methods: {
    async back(){
      await this.postPageState("init", "instance");
    },
    async deploy(){
      if(this.wallet !== this.config.owner) throw "Owners do not match";
      const [tx, signers, config] = await deployInstanceTx(this.config);
      this.config = config;
      if(await this.signTx(tx, signers, this.log)){
        await this.post(this.config, "/set_config")
        await this.postPageState("deployed", "instance");
        this.show(true, "Successfully deployed");
      }
    }
  },
  data(){
    return{
      config: Config,
      displayTime: displayTime,
      BigInteger: BigInteger,
      displayInstanceSpec: displayInstanceSpec
    }
  },
}
</script>