<template>
  <div class="row btn_row">
    <PurpleButton text="Reconfigure" :on-click="wE(this.back)" :is-running="isRunning" :mutable="true"/>
    <BlueButton text="Verify Upload" :on-click="wE(this.verify)" :disabled="isVerified" :is-running="isRunning" :mutable="true">
    <p>Already verified!</p></BlueButton>
  </div>
  <MessagePool v-if="isVerified" title="Upload Verified and Ready to Launch" class="blue_back"/>
  <div class="container1 container rounded-4 pool_back">
    <div class="row"><h5 class="purple_letter">Reward Tokens</h5></div>
    <div class="row" style="margin-top: 0; padding-top: 0">
      <DisplayTrait name="Reward Tokens" style="color: transparent"/>
      <div v-for="index in this.config.tokens.length" :key="index">
        <DisplayToken :value="config.tokens[index-1]" :is-short="true"/>
      </div>
    </div>
    <div class="hr grey_back" style="margin-top: 0"/>
    <div class="row" style="margin-top: 0; padding-top: 0">
      <DisplayTrait name="Base Values"/>
      <div v-for="index in this.config.tokens.length" :key="index" class="white_letter" style="width: 200px; padding-top: 5px; text-align: end">
        {{displayFloat(this.values.base_value[index-1], this.config.decimals[index-1])}}
      </div>
    </div>
  </div>
  <div class="container1 container rounded-4 pool_back" style="padding-top: 0px" v-for="trait_type in Object.getOwnPropertyNames(values.used_traits)" :key="trait_type" >
    <div class="row">
      <h6 class="purple_letter">{{trait_type}}</h6>
    </div>
    <div class="hr grey_back" style="margin-top: 0"/>
    <div class="row" style="margin-top: 0; padding-top: 0" v-for="trait in Object.getOwnPropertyNames(this.values.used_traits[trait_type])" :key="trait">
      <DisplayTrait :name="trait"/>
      <div v-for="j in this.config.tokens.length" :key="j" class="white_letter" style="width: 200px; padding-top: 5px; text-align: end">
        {{displayFloat(this.values.used_traits[trait_type][trait][j-1], this.config.decimals[j-1])}}
      </div>
    </div>
  </div>
</template>

<script>

import Config from "../../data/config.json";
import DisplayToken from "@/elements/DisplayToken";
import BlueButton from "@/elements/BlueButton";
import DisplayTrait from "@/elements/DisplayTrait";
import PurpleButton from "@/elements/PurpleButton";
import {displayFloat} from "@/methods";
import MessagePool from "@/elements/MessagePool";
export default {
  name: "RewardsState",
  components: {
    MessagePool,
    DisplayToken,
    BlueButton,
    DisplayTrait,
    PurpleButton
  },
  props: ['show', 'isRunning', 'state', 'wE', 'post', 'postPageState', 'isVerified'],
  methods:{
    async verify(){
      await this.post({}, "/verify_upload");
    },
    async back(){
      await this.postPageState("configure", "rewards");
    }
  },
  async mounted() {
    const answer = await this.post({}, "/get_values");
    if(!answer) return;
    this.values = answer.values;
  },
  data(){
    return{
      config: Config,
      values: {base_value: ["0", "0", "0", "0", "0"], used_traits: {}},
      displayFloat: displayFloat,
    }
  }
}
</script>

<style scoped>
.input{
  background: transparent;
  width: 125px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-left-color: transparent;
  border-bottom: transparent;
  text-align: right;
}
.input::placeholder {
  text-align: right;
}
</style>