<template>
  <div v-if="!this.isReady" class="container1 container rounded-4 pool_back">
    <div class="row">
      <h5 class="purple_letter">State</h5>
      <h5 class="white_letter">Undefined</h5>
      <ReloadButton :func="this.refresh"/>
    </div>
    <DisplayRow symbol="Address" :value="this.config.instance" :underline="true"/>
  </div>
  <div v-else class="container1 container rounded-4 pool_back">
    <div class="row">
      <h5 class="purple_letter">State</h5>
      <h5 class="white_letter">{{displayInstanceState(this.instance.state)}}</h5>
      <ReloadButton :func="this.refresh"/>
    </div>
    <DisplayRow symbol="Address" :value="this.config.instance" :underline="true"/>
    <DisplayRow symbol="Owner" :value="this.instance.owner" :underline="true"/>
    <DisplayRow symbol="Collection" :value="this.instance.collection" :underline="true" :is-token="true"/>
    <DisplayRow symbol="Loader" :value="this.instance.loader" :underline="true"/>
    <DisplayRow symbol="Specification" :value="displayInstanceSpec(this.instance.spec)"/>
    <DisplayRow symbol="Unit Time" :value="displayTime(this.instance.unit_time)"/>
    <DisplayRow symbol="Update Time" :value="displayDate(this.instance.update_time)" :underline="true"/>
    <div class="row">
      <h5 class="purple_letter">Reward Tokens</h5>
    </div>
    <div v-for="index in this.instance.deposits.length" :key="index">
      <DisplayRow symbol="" :value="this.config.tokens[index-1]" is-token="true"/>
      <DisplayRow symbol="Balance" :value="displayFloat(this.instance.deposits[index-1].balance, this.config.decimals[index-1])" :is-white="true"/>
      <DisplayRow symbol="Pledge" :value="displayFloat(this.instance.deposits[index-1].pledge, this.config.decimals[index-1])" :is-white="true"/>
    </div>
  </div>
  <div class="row btn_row" v-if="this.isReady">
    <BlueButton v-if="this.instance.state===1" text="Pause" :on-click="wE(pause)" :mutable="true" :is-running="isRunning"/>
    <PurpleButton v-else text="Launch" :on-click="wE(launch)" :mutable="true" :is-running="isRunning"/>
    <PurpleButton v-if="this.instance.state===2||this.instance.state===0" text="Set Unit Time" :on-click="wE(setUnitTime)" :mutable="true" :is-running="isRunning"/>
    <PurpleButton text="Add Balance" :on-click="this.openAddBalanceAlert"/>
  </div>
  <AddBalanceAlert :w-e="wE" :log="log" :show="show" :sign-tx="signTx"  id="add-balance-alert" :open="openAddBalanceAlert"/>
  <FirstLaunchAlert :w-e="wE" :log="log" :show="show" :sign-tx="signTx"  id="first-launch-alert" :open="openFirstLaunchAlert"/>
</template>

<script>
import PurpleButton from "@/elements/PurpleButton";
import DisplayRow from "@/elements/DisplayRow";
import Config from "../../data/config.json";
import {
  displayDate,
  displayFloat,
  displayInstanceSpec,
  displayInstanceState,
  displayTime,
  parseTime,
  pauseTx, secondLaunchTx,
} from "@/methods";
import {timeInput} from "@/templates";
import ReloadButton from "@/elements/ReloadButton2";
import BlueButton from "@/elements/BlueButton";
import Swal from "sweetalert2";
import BigInteger from "big-integer";
import {setUnitTimeTx} from "@/methods";
import AddBalanceAlert from "@/pages/AddBalanceAlert";
import FirstLaunchAlert from "@/pages/FirstLaunchAlert";
export default {
  name: "InstanceState",
  props: ["wE", 'wallet', 'isRunning', 'post', 'signTx', 'show', 'log', 'instance', 'refreshInstance'],
  components: {
    FirstLaunchAlert,
    AddBalanceAlert,
    BlueButton,
    PurpleButton,
    DisplayRow,
    ReloadButton
  },
  methods: {
    async refresh(){
      this.isReady = !((await this.refreshInstance())===undefined);
    },
    async launch(){
      if(this.instance.state===2){
        await this.secondLaunch();
      }
      else{
        await this.firstLaunch();
      }
    },
    async firstLaunch(){
      await this.openFirstLaunchAlert(false);
    },
    async secondLaunch(){
      if (this.wallet===this.config.wallet){
        throw "Owners not match";
      }
      await Swal.fire({
        position: 'center',
        imageHeight: 300,
        icon: 'warning',
        iconColor: "#F3683D",
        text: "People would be able to stake again.",
        background: "#0E0F28",
        color: "#F3683D",
        inputAttributes: {
          autocapitalize: 'off'
        },
        confirmButtonColor: "#2ED69B",
        cancelButtonColor: "#d96868",
        showCancelButton: true,
        confirmButtonText: 'Launch',
        showLoaderOnConfirm: true,
        preConfirm: this.wE(async () => {
          const tx = await secondLaunchTx(this.config);
          await this.signTx(tx, [], this.log);
        }),
      })
    },
    async pause(){
      if (this.wallet===this.config.wallet){
        throw "Owners not match";
      }
      await Swal.fire({
        position: 'center',
        imageHeight: 300,
        icon: 'warning',
        iconColor: "#F3683D",
        text: "People would not be able stake after this transaction unitl you launch it again. Perhaps any staked nft could be unstaked anytime.",
        background: "#0E0F28",
        color: "#F3683D",
        inputAttributes: {
          autocapitalize: 'off'
        },
        confirmButtonColor: "#2ED69B",
        cancelButtonColor: "#d96868",
        showCancelButton: true,
        confirmButtonText: 'Pause',
        showLoaderOnConfirm: true,
        preConfirm: this.wE(async () => {
          const tx = await pauseTx(this.config);
          await this.signTx(tx, [], this.log);
        }),
      })
    },
    async setUnitTime(){
      if (this.wallet===this.config.wallet){
        throw "Owners not match";
      }
      await Swal.fire({
        title: "Time",
        html: timeInput,
        background: "#0E0F28",
        color: "#d9d9d9",
        inputAttributes: {
          autocapitalize: 'off'
        },
        confirmButtonColor: "#2ED69B",
        cancelButtonColor: "#d96868",
        showCancelButton: true,
        confirmButtonText: 'Set',
        showLoaderOnConfirm: true,
        preConfirm: this.wE(async () => {
          const day = document.getElementById("day-input").value;
          const hour = document.getElementById("hour-input").value;
          const min = document.getElementById("min-input").value;
          const sec = document.getElementById("sec-input").value;
          const unix = parseTime(day, hour, min, sec);
          const time = displayTime(BigInteger(unix));
          await Swal.fire({
            title: 'Time',
            text: time,
            background: "#0E0F28",
            color: "#d9d9d9",
            inputAttributes: {
              autocapitalize: 'off'
            },
            confirmButtonColor: "#2ED69B",
            cancelButtonColor: "#d96868",
            showCancelButton: true,
            confirmButtonText: 'Confirm',
            showLoaderOnConfirm: true,
            preConfirm: this.wE(async () => {
              await this.signTx(setUnitTimeTx(this.config, unix), [], this.log);
            }),
          });
        }),
      });
    },
    async openAddBalanceAlert(not){
      if(not){
        document.getElementById("add-balance-alert").style.display = "none";

      }else{
        document.getElementById("add-balance-alert").style.display = "grid";
      }
    },
    async openFirstLaunchAlert(not){
      if(not){
        document.getElementById("first-launch-alert").style.display = "none";

      }else{
        document.getElementById("first-launch-alert").style.display = "grid";
      }
    },
  },
  async mounted() {
    await this.refresh();
  },
  data(){
    return{
      config: Config,
      isReady: false,
      displayInstanceSpec: displayInstanceSpec,
      displayInstanceState: displayInstanceState,
      displayTime: displayTime,
      displayDate: displayDate,
      displayFloat: displayFloat,
    }
  },
}
</script>

<style scoped>

</style>