<template>
  <div class="row_log page_background rounded-1">
    <div v-if="msg.type===0" class="blue_letter text" style="white-space: nowrap;">{{msg.text}}</div>
    <div v-else-if="msg.type===1" class="success_letter text" style="white-space: nowrap;">{{msg.text}}</div>
    <div v-else-if="msg.type===2" class="error_letter text" style="white-space: nowrap;">{{msg.text}}</div>
    <div v-else class="white_letter text" style="white-space: nowrap;">{{msg.text}}</div>
    <span class="white_hover" v-if="msg.type===0 || msg.type===2"
          style="font-size: 20px; margin-right: 0px; margin-left: auto;"
          v-on:click="copy()">&boxbox;</span>
  </div>
</template>

<script>
export default {
  name: "LogLine",
  props: ['msg'],
  methods: {
    copy(){
      if(navigator.clipboard){
        navigator.clipboard.writeText(this.msg.text).then(() => {
          //console.log("Copied");
        }, (err) => {
          console.log(err);
        });
      } else if (window.clipboardData){
        window.clipboardData.setData("Text", this.msg.text);
      }
    }
  }
}
</script>

<style scoped>
.row_log{
  display: grid;
  grid-auto-flow: column;
  padding: 0;
  margin-top: 0;
  margin-bottom: 3px;
  margin-left: 5px;
  margin-right: 5px;
  justify-content: space-between;
}
.text{
  text-overflow: ellipsis;
  overflow: hidden;
}
</style>