<template>
  <div class="row btn_row">
    <PurpleButton text="Configure" :on-click="wE(this.configure)"/>
    <PurpleButton text="Load" :on-click="wE(this.load)" v-if="activeIndex>=0"/>
  </div>
  <div class="hr grey_back btn_row" style="margin-top: 10px; margin-bottom: 10px;"></div>
  <div v-for="index in this.instances.length" :key="index" class="btn_row">
    <button type="button" class="pool_back instance_btn rounded-3" v-on:click="activeIndex=index" :disabled="index===activeIndex">
      <DisplayRow symbol="Address" :value="instances[index-1].address" :is-white="true" :underline="true"/>
      <DisplayRow symbol="Collection" :value="instances[index-1].collection" :is-token="true"/>
      <DisplayRow symbol="Update Time" :value="displayDate(instances[index-1].update_time)"/>
    </button>
  </div>
  <div v-if="!isReady" class="spinner-border pale_letter" role="status"/>
</template>

<script>
import {displayDate, searchForInstances} from "@/methods";
import {PublicKey} from "@solana/web3.js";
import PurpleButton from "@/elements/PurpleButton";
import DisplayRow from "@/elements/DisplayRow";
import {useLocalStorage} from '@vueuse/core';
export default {
  name: "InstanceNone",
  components: {
    DisplayRow,
    PurpleButton
  },
  props: ['post', 'postPageState', 'wallet', 'wE', 'show'],
  methods: {
    async configure(){
      await this.postPageState("init", "instance");
    },
    async load(){
      const address = this.instances[this.activeIndex-1].address;
      if(await this.post({address: address}, "/load_instance")){
        await this.postPageState("deployed", "instance");
        window.location = "/"
      }
    },
    async delay(delayInms){
      return new Promise(resolve => setTimeout(resolve, delayInms));
    },
    async refresh(){
      await this.delay(500);
      if(this.wallet==='none') return;
      this.instances = await searchForInstances(new PublicKey(this.wallet));
      this.isReady = true;
    }
  },
  mounted() {
    this.refresh();
  },
  data(){
    return{
      instances: useLocalStorage('instancesSearched', []),
      activeIndex: -1,
      displayDate: displayDate,
      isReady: false
    }
  }
}
</script>

<style scoped>
.instance_btn{
  padding-bottom: 10px;
  margin-bottom: 10px;
}
.pale_letter{
  justify-self: center;
  width: 38px;
  height: 38px;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 37px;
  margin-left: 37px;

}
</style>