<template>
  <div class="row btn_row">
    <PurpleButton text="Back" :on-click="wE(this.back)" :is-running="isRunning" :mutable="true"/>
    <BlueButton text="Flush" :on-click="wE(this.flush)"/>
  </div>
  <div class="container1 container rounded-4 pool_back">
    <div class="row"><h5 class="purple_letter">Reward Tokens</h5></div>
    <div class="row" style="margin-top: 0; padding-top: 0">
      <DisplayTrait name="Reward Tokens" style="color: transparent"/>
      <div v-for="index in this.config.tokens.length" :key="index">
        <DisplayToken :value="config.tokens[index-1]" :is-short="true"/>
      </div>
    </div>
    <div class="hr grey_back" style="margin-top: 0"/>
    <div class="row" style="margin-top: 0; padding-top: 0">
      <DisplayTrait name="Base Values"/>
      <div v-for="index in this.config.tokens.length" :key="index">
        <input class="input white_letter"
               :value="this.base_values[index-1]"
               @input="event => this.base_values[index-1] = event.target.value"
               placeholder="0.0" aria-label="reward" aria-describedby="basic-addon3">
      </div>
    </div>
  </div>
  <div v-for="index in this.traits.length" :key="index" class="container1 container rounded-4 pool_back" style="padding-top: 0px">
    <div class="row">
      <h6 class="purple_letter">{{traits[index-1].trait_type}}</h6>
      <div class="form-check form-switch" style="padding: 0">
        <input class="form-check-input" type="checkbox" id="flexSwitchCheckDefault" :checked="this.usedTraits[traits[index-1].trait_type]!==undefined"
               @input="event => this.useTrait(traits[index-1].trait_type, index-1)"/>
      </div>
    </div>
    <div class="hr grey_back" style="margin-top: 0"/>
    <div v-if="this.usedTraits[traits[index-1].trait_type]!==undefined" style="padding: 0px; margin-top: 0px;">
      <div class="row" style="margin-top: 0; padding-top: 0" v-for="type in traits[index-1].values" :key="type">
        <DisplayTrait :name="type"/>
        <div v-for="j in this.config.tokens.length" :key="j">
          <input class="input white_letter"
                 :value="this.usedTraits[traits[index-1].trait_type][type][j-1]"
                 @input="event => this.usedTraits[traits[index-1].trait_type][type][j-1] = event.target.value"
                 placeholder="0.0" aria-label="reward" aria-describedby="basic-addon3">
        </div>
      </div>
    </div>
  </div>
  <PurpleButton text="Configure" :on-click="wE(this.configure)" :mutable="true"
                :is-running="isRunning"/>
</template>

<script>
import Config from "../../data/config.json";
import DisplayToken from "@/elements/DisplayToken";
import BlueButton from "@/elements/BlueButton";
import DisplayTrait from "@/elements/DisplayTrait";
import PurpleButton from "@/elements/PurpleButton";
import {useLocalStorage} from '@vueuse/core';
import {parseFloat} from "@/methods";
export default {
  name: "RewardsConfigure",
  components: {
    DisplayToken,
    BlueButton,
    DisplayTrait,
    PurpleButton
  },
  props: ['show', 'isRunning', 'wE', 'post', 'postPageState'],
  methods:{
    flush(){
      this.base_values = Array(5).fill("0");
      for(let trait_type in this.usedTraits) {
        for (let trait in this.usedTraits[trait_type]) {
          this.usedTraits[trait_type][trait] = Array(this.config.tokens.length).fill("0");
        }
      }
    },
    async back(){
      await this.postPageState("none", "rewards");
    },
    async configure(){
      let values = {base_value: [], used_traits: {}};
      for(let i = 0; i < this.config.tokens.length; i++){
        values.base_value.push(parseFloat(this.base_values[i], this.config.decimals[i]));
      }
      for(let trait_type in this.usedTraits){
        values.used_traits[trait_type] = {};
        for(let trait in this.usedTraits[trait_type]){
          values.used_traits[trait_type][trait] = [];
          for(let i = 0; i < this.config.tokens.length; i++){
            values.used_traits[trait_type][trait].push(parseFloat(this.usedTraits[trait_type][trait][i], this.config.decimals[i]))
          }
        }
      }
      if(await this.post(values, "/set_values")){
        await this.postPageState("scripted", "rewards");
      }
    },
    useTrait(type, index){
      if(this.usedTraits[type]===undefined){
        let a = {};
        const values = this.traits[index].values;
        const length = this.config.tokens.length;
        for(let name in values){
          a[values[name]] = Array(length).fill("0");
        }
        this.usedTraits[type]=a;
      }
      else{
        delete this.usedTraits[type];
      }
    }
  },
  async mounted() {
    const answer = await this.post({}, "/get_traits", true);
    if(!answer){
      await this.postPageState("none", "rewards");
    }
    else{
      this.traits = answer.traits.traits;
    }
  },
  data(){
    return{
      config: Config,
      base_values: useLocalStorage("base_values", ["0", "0", "0", "0", "0"]),
      traits: useLocalStorage("traits", []),
      usedTraits: useLocalStorage("usedTraits", {})
    }
  }
}
</script>

<style scoped>
.input{
  background: transparent;
  width: 125px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-left-color: transparent;
  border-bottom: transparent;
  text-align: right;
}
.input::placeholder {
  text-align: right;
}
</style>


