<template>
  <div v-if="this.loader !== 'none'" class="container1 container rounded-4 pool_back">
    <div class="row">
      <h5 class="blue_letter text">Loader</h5>
      <div class="white_letter text">{{this.loader}}</div>
    </div>
    <div class="hr grey_back"></div>
    <div class="row">
      <h6 class="blue_letter text">
        <ReloadButton :func="this.refreshBalance" style="padding: 0"/>
        Balance</h6>
      <div class="white_letter text" style="align-self: end; margin-left: auto">{{this.balance}}</div>
    </div>
  </div>
  <div v-if="this.loader !== 'none'" class="row btn_row">
    <PurpleButton text="Fund" :on-click="wE(this.fund)"></PurpleButton>
    <PurpleButton v-if="this.config.loader !== loader" text="Update On Chain" :on-click="wE(this.updateOnChain)"
                  :is-running="isRunning" :mutable="true" :disabled="instance? this.instance.state===1: true">
    <p>Pause Instance first!</p></PurpleButton>
    <BlueButton text="Withdraw All" :on-click="wE(this.withdraw)" :is-running="isRunning" :mutable="true"/>
  </div>
  <MessagePool v-else class="error_back" title="No Loader Keypair found"
  text="input keypair to ./data/loader-keypair.json"/>
</template>

<script>
import PurpleButton from "@/elements/PurpleButton";
import Config from "../../data/config.json";
import {useLocalStorage} from "@vueuse/core/index";
import {getBalance, displayFloat, setLoaderTx, parseFloat, fundLoaderTx} from "@/methods";
import {PublicKey} from "@solana/web3.js";
import ReloadButton from "@/elements/ReloadButton";
import MessagePool from "@/elements/MessagePool";
import BlueButton from "@/elements/BlueButton";
import Swal from "sweetalert2";
export default {
  name: "LoaderState",
  props: ['isRunning', 'post', 'signTx', 'wE', 'wallet', 'show', 'log', 'instance'],
  components: {
    BlueButton,
    PurpleButton,
    ReloadButton,
    MessagePool
  },
  methods:{
    async refreshBalance(){
      const num = await getBalance(new PublicKey(this.loader));
      this.balance = displayFloat(num , 9);
    },
    async withdraw(){
      if(this.wallet !== this.config.owner) throw "Owners do not match";
      if(await this.post({}, "/loader_withdraw")) await this.refreshBalance();
    },
    async fund(){
      if (this.wallet==='none'){
        throw "Connect wallet first";
      }
      await Swal.fire({
        title: 'Amount',
        input: 'text',
        inputPlaceholder: "0.0",
        background: "#0E0F28",
        color: "#FFFDFD",
        inputAttributes: {
          autocapitalize: 'off'
        },
        confirmButtonColor: "#2ED69B",
        cancelButtonColor: "#d96868",
        showCancelButton: true,
        confirmButtonText: 'Fund',
        showLoaderOnConfirm: true,
        preConfirm: async (amount) => {
          try{
            amount = parseFloat(amount, 9);
          }
          catch(e){
            this.show(false, "Input valid number");
            return;
          }
          if(await this.signTx(fundLoaderTx(this.wallet, this.loader, amount), [], this.log)){
            this.show(true, "Funded loader");
            this.refreshBalance();
          }
        },
      });
    },
    async updateOnChain(){
      if(this.wallet !== this.config.owner) throw "Owners do not match";
      if(await this.signTx(await setLoaderTx(this.config, this.loader), [], this.log)){
        this.config.loader = this.loader;
        if(await this.post(this.config, "/set_config")){
          this.show(true, "Changed loader address");
        }
      }
    }
  },
  async mounted() {
    const answer = await this.post({}, "/get_loader");
    this.loader = answer? answer.loader: "none";
    if(answer) await this.refreshBalance();
  },
  data(){
    return{
      loader: useLocalStorage('loaderPubkey', "none"),
      balance: "none",
      config: Config
    }
  },
}
</script>

<style scoped>
.text{
  padding: 0px 10px 0px 10px;
}
</style>